@import "../../global.scss";

.testimonials{
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    @include mobile{
        justify-content: space-between;
    }

    h1{
        font-size: 50px;
        @include mobile{
            font-size: 30px;
        }
    }
    .container{
        width: 100%;
        height: 80%;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-bottom: 30px;
        

        @include mobile{
            flex-direction: column;
        }

        .card{
            width: 250px;
            height: 70%;
            border-radius: 10px;
            box-shadow: 0px 0px 15px -8px black;
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            padding: 20px;
            transition: all 0.5s ease;

            @include mobile{
                height: 180px;
            }

            &.featured{
                width: 300px;
                height: 75%;
                margin: 0 30px;

                @include mobile{
                    width: 250px;
                    height: 180px; 
                    margin: 10px;
                }
            }

            &:hover{
                transform: scale(1.1);

                @include mobile{
                    transform: none;
                }
            }

            .top{
                display: flex;
                align-items: center;
                justify-content: center;
                
                img{
                    &.left, &.right{
                        height: 25px;
                    }
                    &.photo{
                        height: 90px;
                        width: 90px;
                        border-radius: 50%;
                        object-fit: cover;
                        margin: 0 30px;

                        @include mobile{
                            height: 40px;
                            width: 40px;
                        }
                    }
                }
            }

            .center{
                padding: 10px;
                background-color: rgb(250, 244, 245);
                border-radius: 10px;

                @include mobile{
                    font-size: 10px;
                    padding: 5px;
                }
            }

            .bottom{
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                
                h3{
                    margin-bottom: 5px;

                    @include mobile{
                        font-size: 13px;
                    }
                }

                h4{
                    color: gray;

                    @include mobile{
                        font-size: 10px;
                    }
                }
            }
        }
    }
}