@import "../../global.scss";

.portfolio{
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;

    h1{
        font-size: 50px;
        @include mobile{
            font-size: 30px;
        }
    }

    ul{
        margin: 10px;
        padding: 0;
        list-style: none;
        display: flex;

        @include mobile{
            flex-wrap: wrap;
            justify-content: center;
            margin-top: 30px;
        }
    }

    .container{
        width: 70%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;

        @include mobile{
            width: 100%;
            margin: 20px;
        }

        .item{
            width: 220px;
            height: 150px;
            border-radius: 20px;
            border: 1px solid rgb(240, 239, 239);
            margin: 10px 20px;
            display: flex;
            align-items: center;
            justify-content: center;
            color: white;
            position: relative;
            transition: all 1s ease;
            cursor: pointer;

            @include mobile{
                width: 140px;
                height: 100px;
                margin: 5px 10px;
            }
            
            h3{
                position: absolute;
                font-size: 20px;
                @include mobile{
                    font-size: 15px;
                }
            }
            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
                z-index: 1;
            }
            &:hover{
                background-color: $mainColor;
                img{
                    z-index: 0;
                    opacity: 0.2;
                    
                }
            }
        }
    }
}